





























































































































































































































@import "../assets/scss/_variables.scss";
.articles {
  height: 100vh;
  overflow: hidden;
  // left
  .left-sidebar {
    background-color: $bg-color;
    background-repeat: no-repeat;
    background-size: 400px;
    background-position-y: bottom;
    background-position-x: right;
    .filter-wrap_box {
      width: 100%;
      max-width: 370px;
      padding-top: 100px;
      padding-right: 100px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      min-height: 850px;
    }
    h1 {
      font-family: "MuseoSansCyrl_700";
    }
    .left_img {
      width: 383px;
      height: 453px;
      background-position: bottom;
      background-size: 100%;
      background-repeat: no-repeat;
    }
  }
  // right
  .content-wrap {
    margin-top: 100px;
    height: calc(100vh - 100px);
    .content-wrap_box {
      height: 100%;
      overflow: hidden;
      overflow-y: scroll;
      .content-wrap_list {
        margin-bottom: 40px;
        padding-bottom: 20px;
        max-width: 675px;
        border-bottom: 1px solid #f9ebe4;
      }
      h2 {
        font-size: 1.1rem;
        font-family: "MuseoSansCyrl_500";
        line-height: 1.2rem;
      }
      .date,
      .category_name {
        color: $light-text-color;
      }
      .date {
        font-size: 0.7rem;
      }
    }
  }
  .filter-mobile {
    margin: 10px 0;
    padding: 15px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px;
    box-shadow: 0px 0px 25px -1px $box-shadow;
    cursor: pointer;
    >.filter-name {
      font-size: 1.2rem;
      font-family: "MuseoSansCyrl_700";
      color: $link-color;
    }
  }
  @media(max-width: 991px) {
    h1 {
      font-family: "MuseoSansCyrl_700" !important;
      font-size: 2rem !important;
    }
  }
}
