
























































































































@import "../assets/scss/_variables.scss";
.about {
  position: relative;
  .mt-100 {
    margin-top: 100px;
  }
}

.border-right-decore::before {
  content: "";
  display: block;
  position: absolute;
  right: 0;
  height: 100%;
  width: 3px;
  background-image: -webkit-gradient(
    linear,
    left top,
    left bottom,
    from(#f7af8c),
    to(#bb785f)
  );
  background-image: linear-gradient(#f7af8c, #bb785f);
}

.right-sidebar {
  position: sticky;
  top: 100px;
  li {
    padding: 5px 0;
    font-size: 1rem;
    cursor: pointer;
    &.active {
      color: $main-color;
      font-family: MuseoSansCyrl_700;
    }
  }
}

.filter-mobile {
  margin: 100px 10px 0;
  padding: 15px;
  width: 100%;
  padding: 15px;
  box-shadow: 0px 0px 25px -1px $box-shadow;
  cursor: pointer;
  .filter-name {
    font-size: 1.2rem;
    font-family: "MuseoSansCyrl_700";
    color: $link-color;
  }
  .right-sidebar {
    padding: 15px;
  }
}

@media (max-width: 991px) {
  .border-right-decore::before {
    background-image: none !important;
    width: 0 !important;
    background-color: white;
  }
  .mt-100 {
    margin-top: 40px !important;
  }
}
