/* ------------------------- */
@font-face {
  font-family: MuseoSansCyrl;
  src: url(./assets/fonts/MuseoSansCyrl.ttf); }

@font-face {
  font-family: MuseoSansCyrl_500;
  src: url(./assets/fonts/MuseoSansCyrl_500.ttf); }

@font-face {
  font-family: MuseoSansCyrl_700;
  src: url(./assets/fonts/MuseoSansCyrl_700.ttf); }

@font-face {
  font-family: MuseoSansCyrl_900;
  src: url(./assets/fonts/MuseoSansCyrl_900.ttf); }

html,
body {
  height: 100%;
  width: 100%; }

html.md-theme-default {
  background-color: #ffffff !important; }

body {
  font-family: MuseoSansCyrl !important;
  color: #000000;
  font-size: 14px;
  position: relative;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased; }

#app {
  position: relative;
  background-color: #ffffff; }

h2,
h3,
b {
  font-family: MuseoSansCyrl_700 !important; }

* {
  box-sizing: border-box; }
  *:active, *:focus {
    outline: none !important; }

::-webkit-scrollbar {
  width: 0px;
  background-color: #ffffff; }

button {
  border: none; }

a,
a:hover {
  text-decoration: underline !important;
  color: #61b826 !important;
  font-family: MuseoSansCyrl_500 !important; }

.spinner {
  height: 100vh;
  width: 100vw;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center; }
  .spinner .v-spinner {
    margin-top: -80px; }

.articles .content_list > p {
  line-height: 1.1rem; }

.tag-news {
  background-color: #8a6ecd; }

.tag-post {
  background-color: #ffbf26; }

.tag-about {
  background-color: lime; }

.tag-stock {
  background-color: #00bada; }

.marker {
  width: 6px;
  height: 6px;
  border-radius: 50%; }

.home,
.contacts,
.post {
  position: fixed;
  top: 100px;
  height: calc(100vh - 80px);
  width: 100vw;
  overflow: hidden;
  overflow-y: auto; }

a.telelegram-bot {
  color: #14a0dc !important; }

.md-switch {
  margin: 10px 0 !important; }

.md-switch label {
  margin-bottom: 0 !important; }

.link-more {
  display: none; }

