  box-shadow: 0px 20px 22px 0px rgba(97, 184, 38, 0.51);
  cursor: pointer;
  -webkit-transition: all 0.3s;
  transition: all 0.3s; }
  .btn-vprimary:hover {
    box-shadow: none; }

.slide-fade-enter-active {
  transition: all .5s ease;
  opacity: 1; }

.slide-fade-leave-active {
  transition: all .5s ease;
  transform: translateY(20px); }

.slide-fade-enter,
.slide-fade-leave-to {
  transition: all .5s ease;
  transform: translateY(-20px);
  opacity: 0; }

.header {
  position: fixed;
