



























































.backLog {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    .blockPhoto {
        max-width: 600px;
        width: 100%;
        height: 400px;
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
    }
    .fa,
    .far {
        font-size: 40px;
        position: absolute;
        color: white;
        cursor: pointer;
        &-arrow-circle-left {
            left: 10%;
        }
        &-arrow-circle-right {
            right: 10%;
        }
        &-times-circle {
            top: 10%;
            right: 10%;
        }
        &r {
            font-size: 30px;
        }
    }
    .disabled {
        opacity: .5;
        cursor: not-allowed;
    }
}
