.establishments {
  height: 100vh;
}

.establishment_list {
  font-size: 0.8rem;
  h2 {
    font-size: 1.15rem;
    font-family: "MuseoSansCyrl_500";
  }
  .type {
    font-size: 0.7rem;
    color: $light-text-color;
  }
  .address {
    &>div:first-child {
      color: $light-text-color;
    }
  }
  .prices {
    &>span:first-child {
      color: $light-text-color;
    }
  }
  .icons {
    line-height: 2;
  }
}

.left-sidebar {
  background-color: $bg-color;
  .filter-wrap_box {
    width: 100%;
    max-width: 370px;
    padding-right: 100px;
    height: calc(100vh - 100px);
    position: fixed;
    top: 100px;
    overflow-y: scroll;
    overflow-x: hidden;
  }
}

.content-wrap {
  margin-top: 100px;
  height: calc(100vh - 100px);
  .content-wrap_box {
    width: 100%;
    height: 100%;
    overflow: hidden;
    overflow-y: scroll;
    .content-wrap_list {
      padding: 40px 20px;
      max-width: 675px;
      &:hover {
        box-shadow: 0px 17px 40px -1px $box-shadow;
      }
      .left-part_list {
        width: 170px;
        margin-right: 15px;
        &>img {
          width: 100%;
        }
      }
      .right-part_list {
        width: 87%;
      }
    }
    .logo-establishment {
      width: 100%;
    }
    .filter-mobile {
      margin: 10px 0;
      padding: 15px;
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 15px;
      box-shadow: 0px 0px 25px -1px $box-shadow;
      cursor: pointer;
      >.filter-name {
        font-size: 1.2rem;
        font-family: "MuseoSansCyrl_700";
        color: $link-color;
      }
    }
  }
}

h2 {
  font-size: 1.1rem;
  font-family: "MuseoSansCyrl_500";
  line-height: 1.2rem;
}

h1 {
  font-family: "MuseoSansCyrl_700";
}

.date,
.category_name {
  color: $light-text-color;
}

.date {
  font-size: 0.7rem;
}

.left_img {
  width: 400px;
  height: 471px;
  background-position: 62% -26%;
  background-size: 126%;
  margin-bottom: -65px;
  background-repeat: no-repeat;
}

.icon-filter {
  width: 20px;
  height: 20px;
}

.md-switch {
  width: 90%;
}

.md-field:last-child::after {
  height: 2px;
}

.filter-mobile {
  margin: 10px 0;
  padding: 15px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  box-shadow: 0px 0px 25px -1px $box-shadow;
  cursor: pointer;
  &>div:first-child {
    font-size: 1.2rem;
    font-family: "MuseoSansCyrl_700";
    color: $link-color;
  }
}

@media (max-width: 991px) {
  .filter-wrap_box {
    transition: all 1s ease;
    padding: 30px;
    h1 {
      font-size: 2rem;
    }
  }
  .content-wrap_box {
    .content-wrap_list {
      padding: 30px !important;
      margin-bottom: 40px !important;
      .logo-establishment {
        width: 60px;
        float: right;
      }
      .right-part_list {
        width: 100% !important;
      }
    }
  }
}
