.aboutRestaurant {
    height: 100vh;
    .left-side {
        background-color: $bg-color;
        .left-side_wrap_box {
            width: 100%;
            max-width: 370px;
            height: calc(100vh - 100px);
            position: fixed;
            top: 100px;
            overflow-y: scroll;
            overflow-x: none;
        }
        .gallery {
            width: 298px;
            .preview {
                height: 200px;
                background-size: cover;
                background-repeat: no-repeat;
                transition: all 1s ease;
                position: relative;
                .fa-arrow-circle-right,
                .fa-arrow-circle-left {
                    color: white;
                    position: absolute;
                    top: 50%;
                    font-size: 1.4rem;
                    cursor: pointer;
                }
                .fa-arrow-circle-right {
                    right: 14px;
                }
                .fa-arrow-circle-left {
                    left: 14px;
                }
                .disabled {
                    opacity: .7;
                    cursor: not-allowed;
                }
            }
            .miniature {
                padding-top: 6px;
                .thumbnail {
                    background-size: cover;
                    background-repeat: no-repeat;
                    width: 70px;
                    height: 55px;
                }
                >div:not(:last-child) {
                    margin-right: 6px;
                }
            }
        }
        .gallery-preview-wrapper {
            width: 100%;
            height: 100%;
            display: flex;
            flex-flow: column;
        }
        .address {
            line-height: 1.3;
            h3 {
                font-size: 1.3rem;
            }
        }
        span {
            color: $light-text-color;
            font-size: .7rem;
        }
    }
    .content_wrap {
        padding-top: 100px;
        .content_wrap_box {
            width: 100%;
            height: 100%;
            overflow: hidden;
            overflow-y: scroll;
        }
        .content_wrap_box__details {
            margin: 40px;
            padding: 20px;
            max-width: 675px;
            .type {
                color: $light-text-color;
                font-size: .8rem;
            }
            .name,
            .featuters>div:first-of-type {
                font-size: 1rem;
                font-family: "MuseoSansCyrl_700";
            }
            .prices>span:first-child {
                font-family: "MuseoSansCyrl_500";
                color: #ccc8c8;
            }
            .icon-filter,
            .socials>img {
                width: 20px;
                height: 20px;
            }
            .menu {
                .btn-vprimary {
                    margin-top: 30%;
                    max-width: 200px;
                }
                .plate {
                    margin-top: 40px;
                    width: 100%;
                    max-width: 200px;
                }
            }
            .posts_wrap {
                border-bottom: 1px solid #f9ebe4;
                width: 100%;
                padding-bottom: 30px;
                h1 {
                    font-size: 2rem;
                    font-family: "MuseoSansCyrl_500";
                }
                .left-part_list {
                    width: 50%;
                    margin-right: 15px;
                }
                .right-part_list {
                    width: 50%;
                }
                h2 {
                    font-size: 1.1rem;
                    font-family: "MuseoSansCyrl_500";
                    line-height: 1.2rem;
                }
                .date,
                .category_name {
                    color: $light-text-color;
                }
                .date {
                    font-size: .7rem;
                }
            }
        }
    }
}
