


























































































































@import "../assets/scss/_variables.scss";
h2 {
  font-size: 1.5rem;
}

.date,
.category_name {
  color: $light-text-color;
}

.date {
  font-size: .7rem;
}

h2 {
  font-size: 1.7rem;
  line-height: 2rem;
  margin-bottom: 1rem;
}

.post_name {
  font-family: "MuseoSansCyrl_500";
}

.read-more,
.content {
  height: calc(100vh - 120px);
  overflow-y: auto;
}

.read-more {
  padding: 0 30px;
  h3 {
    font-size: 1rem;
    line-height: 1.2;
  }
  .content_list {
    font-size: .8rem;
    line-height: 1.2;
  }
  &>div {
    margin-bottom: 30px;
  }
}

.content {
  padding-right: 20px;
  &::before {
    content: "";
    display: block;
    position: absolute;
    right: 0;
    height: 120%;
    width: 3px;
    background-image: -webkit-gradient(linear, left top, left bottom, from(#f7af8c), to(#bb785f));
    background-image: linear-gradient(#f7af8c, #bb785f);
  }
}

@media(max-width: 991px) {
  .content {
    height: auto !important;
    overflow: hidden;
    &::before {
      background: transparent !important;
    }
  }
  .read-more {
    margin-top: 30px;
    height: 100% !important;
    overflow: hidden;
  }
}
